/* stylelint-disable */
@font-face {
  font-family: "Popins-reg";
  src: url("../fonts/../fonts/Poppins-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Popins-semb";
  src: url("../fonts/../fonts/Poppins-SemiBold.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
.burgundy-btn {
  color: #FFFFFF;
  border-radius: 8px;
  background: #691A2E;
  box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.1), inset 0px 4px 0px rgba(255, 255, 255, 0.2);
  transition: background-color 0.3s ease;
}
.burgundy-btn:hover {
  background: #521424;
}

.orange-btn {
  border-radius: 8px;
  background: #E1712D;
  box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.1), inset 0px 4px 0px rgba(255, 255, 255, 0.2);
  color: black;
  transition: background-color 0.3s ease;
}
.orange-btn:hover {
  background: #C26226;
}

.grey-btn {
  border-radius: 8px;
  color: #FFFFFF;
  background: rgba(255, 255, 255, 0.2);
  box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.1), inset 0px 4px 0px rgba(255, 255, 255, 0.2);
  transition: background-color 0.3s ease;
}
.grey-btn:hover {
  background: rgba(255, 255, 255, 0.1);
}

.grey-btn-two {
  border-radius: 8px;
  color: black;
  background: #E1712D;
  box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.1), inset 0px 4px 0px rgba(255, 255, 255, 0.2);
  transition: background-color 0.3s ease;
}
.grey-btn-two:hover {
  background: #C26226;
}

* {
  padding: 0;
  margin: 0;
  border: 0;
}

*::-webkit-scrollbar {
  width: 0;
  height: 0;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: inherit;
}

img,
picture {
  width: auto;
  height: auto;
  max-width: 100%;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

.main {
  flex: 1 1 auto;
}

@media screen and (max-width: 1024px) {
  .wrapper {
    overflow: hidden;
  }
}
[class*=__container] {
  max-width: 1270px;
  margin: 0 auto;
  padding: 0 15px;
}

body {
  font-family: "Popins-reg", sans-serif;
  font-style: normal;
  font-size: 20px;
  letter-spacing: 0.02em;
  color: #111111;
  background: transparent;
}
body._lock {
  overflow: hidden;
}

.casino-slot {
  background: #FCEDF0;
  margin: 0 0 20px 0;
}
.casino-slot._onWhite {
  padding: 15px;
  border-radius: 8px;
}
@media screen and (max-width: 350px) {
  .casino-slot._onWhite {
    padding: 5px;
  }
}
.casino-slot__container {
  display: flex;
  flex-direction: column;
  padding: 0;
}
.casino-slot__link {
  font-family: "Popins-semb", sans-serif;
  font-size: 17px;
  line-height: 20px;
  letter-spacing: 0.02em;
  color: #FFFFFF;
  align-self: center;
  text-align: center;
  padding: 19px 85px 17px;
}
.casino-slot__list {
  display: grid;
  grid-template-columns: auto auto auto;
  gap: 15px;
  margin: 0 0 30px 0;
}
.casino-slot__item {
  display: flex;
  flex-direction: column;
  padding: 20px;
  border-radius: 12px;
  background: #FFFFFF;
  position: relative;
  max-width: 330px;
}
.casino-slot__item-info {
  position: absolute;
  cursor: pointer;
  top: 20px;
  right: 20px;
  padding: 10.5px;
}
.casino-slot__item-info-text {
  display: none;
  padding: 15px;
  border-radius: 8px;
  background: #691A2E;
  color: #FFFFFF;
  width: 150px;
  position: absolute;
  top: 50px;
  right: 0;
  font-size: 12px;
  line-height: 15px;
}
.casino-slot__item-info-text._show {
  display: block;
}
.casino-slot__item-image {
  max-width: 160px;
  margin: 0 0 10px 0;
}
.casino-slot__item-image img {
  border-radius: 8px;
}
.casino-slot__item-title {
  flex: 1 1 auto;
  font-size: 17px;
  line-height: 26px;
  letter-spacing: -0.004em;
  color: #111111;
  margin: 0 0 2px 0;
}
.casino-slot__item-licensed {
  display: flex;
  color: #2275D4;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: -0.004em;
  margin: 0 0 20px 0;
}
.casino-slot__item-licensed-one {
  margin: 0 8px 0 0;
}
.casino-slot__item-link {
  align-self: flex-start;
  display: inline-block;
  min-width: 160px;
  text-align: center;
  padding: 12px 30px 10px;
  font-size: 15px;
  line-height: 20px;
}

@media screen and (max-width: 1240px) {
  .casino-slot__list {
    display: grid;
    grid-template-columns: calc(50% - 7.5px) calc(50% - 7.5px);
    gap: 15px;
    margin: 0 0 30px 0;
  }
  .casino-slot__item {
    max-width: 100%;
  }
}
@media screen and (max-width: 1024px) {
  .casino-slot__list {
    grid-template-columns: calc(33.333% - 10px) calc(33.333% - 10px) calc(33.333% - 10px);
  }
}
@media screen and (max-width: 830px) {
  .casino-slot__list {
    grid-template-columns: calc(50% - 7.5px) calc(50% - 7.5px);
  }
}
@media screen and (max-width: 600px) {
  .casino-slot__link {
    width: calc(100% - 30px);
  }
  .casino-slot__list {
    grid-template-columns: 100%;
    gap: 15px;
  }
}